import { io } from 'socket.io-client';

// Hardcoded backup URL for Azure
const AZURE_BACKUP_URL = 'https://sales-agent-c3f5ecevdefjcafc.canadacentral-01.azurewebsites.net';

// Get the base URL with fallback
const getBaseUrl = () => {
  if (process.env.NODE_ENV === 'production') {
    return AZURE_BACKUP_URL;
  }
  return 'http://localhost:3001';
};

// Create socket instance with environment-aware configuration
export const socket = io(getBaseUrl(), {
  transports: ['websocket', 'polling'],
  autoConnect: true,
  reconnection: true,
  reconnectionAttempts: 5,
  reconnectionDelay: 1000,
  path: '/socket.io', // Explicitly set the Socket.IO path
  withCredentials: true, // Enable CORS credentials
});

// Track if we've tried the backup URL
let triedBackupUrl = false;

// Socket event listeners for debugging
socket.on('connect', () => {
  console.log('🔌 Socket connected:', socket.id);
  console.log('🔗 Connected to URL:', getBaseUrl());
});

socket.on('disconnect', (reason) => {
  console.log('❌ Socket disconnected:', reason);
});

socket.on('connect_error', (error) => {
  console.error('🚫 Socket connection error:', error);
  console.log('🔍 Attempted connection URL:', getBaseUrl());
  console.log('🌐 Current hostname:', window.location.hostname);
  
  // If we get a connection error and we're on Azure, try the backup URL
  if (window.location.hostname.includes('azurewebsites.net') && !triedBackupUrl) {
    console.log('⚠️ Connection failed on Azure, creating new connection to backup URL:', AZURE_BACKUP_URL);
    triedBackupUrl = true;
    
    // Disconnect the current socket
    socket.disconnect();
    
    // Create a new connection to the backup URL
    const backupSocket = io(AZURE_BACKUP_URL, {
      transports: ['websocket', 'polling'],
      autoConnect: true,
      reconnection: true,
      reconnectionAttempts: 5,
      reconnectionDelay: 1000,
      path: '/socket.io',
      withCredentials: true,
    });
    
    // Transfer all event listeners to the new socket
    Object.assign(socket, backupSocket);
  }
});

export default socket; 