import axios from 'axios';

// Hardcoded backup URL for Azure
const PRODUCTION_URL = 'https://sales-agent-c3f5ecevdefjcafc.canadacentral-01.azurewebsites.net';

// Get the base URL with fallback
const getBaseUrl = () => {
  if (process.env.NODE_ENV === 'production') {
    return PRODUCTION_URL;
  }
  return 'http://localhost:3001';
};

// Create axios instance with environment-aware configuration
const api = axios.create({
  baseURL: getBaseUrl(),
  withCredentials: true, // Enable CORS credentials
});

export default api; 